import React, { useEffect, useState } from "react";
import "./MessageTemplate.css";
import { JAIDA_MSG_ICON } from "../../utilities";

function MessageTemplate(data) {
  const chatWindowInstance = data.hostInstance;
  const helpers = chatWindowInstance.helpers;
  const msgData = data.msgData;
  const [extension, setExtension] = useState("");
  const [formattedDate, setFormattedDate] = useState("");

  useEffect(() => {
    if (
      msgData.message &&
      msgData.message[0] &&
      msgData.message[0].cInfo &&
      msgData.message[0].cInfo.attachments &&
      msgData.message[0].cInfo.attachments.length &&
      msgData.message[0].cInfo.attachments[0].fileName
    ) {
      setExtension(msgData.message[0].cInfo.attachments[0].fileName.split("."));
    } else if (
      msgData?.message[0]?.component?.payload?.template_type === "live_agent"
    ) {
      msgData.fromAgent = true;

      if (
        msgData.message[0].component &&
        msgData.message[0].component.payload
      ) {
        msgData.message[0].cInfo.body =
          msgData.message[0].component.payload.text || "";
      }
    }
    try {
      let messageDate = new Date(msgData?.createdOn);
      setFormattedDate(
        new Intl.DateTimeFormat("en-US", {
          hour12: true,
          hour: "2-digit",
          minute: "numeric",
        }).format(messageDate)
      );
    } catch (error) {
      let currentDate = Date.now();
      setFormattedDate(
        new Intl.DateTimeFormat("en-US", {
          hour12: true,
          hour: "2-digit",
          minute: "2-digit",
        }).format(currentDate)
      );
    }
  }, [msgData]);

  if (!msgData || !msgData.message) {
    return null;
  }

  return (
    <>
      {msgData.message.map((msgItem, key) => {
        if (msgItem.cInfo && msgItem.type === "text") {
          return (
            <div
              key={key}
              className={
                msgData.type === "bot_response"
                  ? "koreBotMessageContainer"
                  : "koreUserMessageContainer"
              }
            >
              {msgData.icon && (
                <div className="kore-profile-photo">
                  <div
                    className="user-account avtar"
                    style={
                      !msgData.fromAgent
                        ? { backgroundImage: `url(${msgData.icon})` }
                        : {}
                    }
                    title="User Avatar"
                  ></div>
                </div>
              )}
              <div className="messageBubble" aria-live="assertive">
                <div>
                  {msgData.type === "bot_response" ? (
                    msgItem.component && msgItem.component.type === "error" ? (
                      <span
                        style={{ color: msgItem.component.payload.color }}
                        dangerouslySetInnerHTML={{
                          __html: helpers.convertMDtoHTML(
                            msgItem.component.payload.text,
                            "bot",
                            msgItem
                          ),
                        }}
                      ></span>
                    ) : (
                      <>
                        <span
                          className="simpleMsg"
                          data-msgdata={JSON.stringify(msgData)}
                          dangerouslySetInnerHTML={{
                            __html: helpers.convertMDtoHTML(
                              msgItem.cInfo.body,
                              "bot",
                              msgItem
                            ),
                          }}
                        ></span>
                        {msgItem.component &&
                          msgItem.component.payload &&
                          msgItem.component.payload.videoUrl && (
                            <div className="videoEle">
                              <video width="300" controls>
                                <source
                                  src={msgItem.component.payload.videoUrl}
                                  type="video/mp4"
                                />
                              </video>
                            </div>
                          )}
                        {msgItem.component &&
                          msgItem.component.payload &&
                          msgItem.component.payload.audioUrl && (
                            <div className="audioEle">
                              <audio width="180" controls>
                                <source
                                  src={msgItem.component.payload.audioUrl}
                                />
                              </audio>
                            </div>
                          )}
                      </>
                    )
                  ) : msgItem.cInfo.renderMsg &&
                    msgItem.cInfo.renderMsg !== "" ? (
                    <span
                      dangerouslySetInnerHTML={{
                        __html: helpers.convertMDtoHTML(
                          msgItem.cInfo.renderMsg,
                          "user",
                          msgItem
                        ),
                      }}
                    ></span>
                  ) : (
                    <span
                      dangerouslySetInnerHTML={{
                        __html: helpers.convertMDtoHTML(
                          msgItem.cInfo.body,
                          "user",
                          msgItem
                        ),
                      }}
                    ></span>
                  )}
                </div>
                {msgItem.cInfo && msgItem.cInfo.emoji && (
                  <span
                    className={`emojione emojione-${msgItem.cInfo.emoji[0].code}`}
                  >
                    {msgItem.cInfo.emoji[0].title}
                  </span>
                )}
                {msgItem.cInfo &&
                  msgItem.cInfo.attachments &&
                  msgData.message[0].cInfo.attachments[0].fileName && (
                    <div
                      className="msgCmpt attachments"
                      fileid={msgItem.cInfo.attachments[0].fileId}
                    >
                      <div className="uploadedFileIcon">
                        {msgItem.cInfo.attachments[0].fileType === "image" ? (
                          <span className="icon cf-icon icon-photos_active"></span>
                        ) : msgItem.cInfo.attachments[0].fileType ===
                          "audio" ? (
                          <span className="icon cf-icon icon-files_audio"></span>
                        ) : msgItem.cInfo.attachments[0].fileType ===
                          "video" ? (
                          <span className="icon cf-icon icon-video_active"></span>
                        ) : (
                          <>
                            {[
                              "xlsx",
                              "xls",
                              "docx",
                              "doc",
                              "pdf",
                              "ppsx",
                              "pptx",
                              "ppt",
                              "zip",
                              "rar",
                            ].includes(extension) ? (
                              <span
                                className={`icon cf-icon icon-files_${extension}`}
                              ></span>
                            ) : (
                              <span className="icon cf-icon icon-files_other_doc"></span>
                            )}
                          </>
                        )}
                      </div>
                      <div className="curUseruploadedFileName">
                        {msgItem.cInfo.attachments[0].fileName}
                      </div>
                    </div>
                  )}
                {msgData.isError && (
                  <div className="errorMsg">Send Failed. Please resend.</div>
                )}
              </div>
              {msgData.createdOn && (
                <div className="extra-info">{formattedDate}</div>
              )}
            </div>
          );
        }
        return null;
      })}
    </>
  );
}

function isMessageTemplate(msgData) {
  if (
    (msgData.message[0] &&
      msgData.message[0].component &&
      msgData.message[0].component.payload &&
      msgData.message[0].component.payload.template_type !== undefined) ||
    (msgData.message[0] &&
      msgData.message[0].component &&
      msgData.message[0].component.payload &&
      msgData.message[0].component.payload.formData &&
      msgData.message[0].component.payload.formData.renderType === "inline")
  ) {
    return false;
  } else {
    return true;
  }
}
export { MessageTemplate, isMessageTemplate };

{
  /* <div className="kore-profile-photo">
                  <div
                    className="messageIconJaida"
                  >
                    {JAIDA_MSG_ICON}
                  </div>
                  <h2 className="messageIconJaidaText">J</h2>
                </div> */
}
