import React, { useEffect, useState } from "react";
import "./jaida_custom_button_template.css";
import { Box, Typography } from "@mui/material";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";
import QuestionMarkIcon from "@mui/icons-material/QuestionMark";
import AutoStoriesOutlinedIcon from "@mui/icons-material/AutoStoriesOutlined";
import FolderOutlinedIcon from "@mui/icons-material/FolderOutlined";
import formatTime, { JAIDA_MSG_ICON } from "../../utilities";
import initiateAgentTransfer from "../../salesForceLiveAgent.js";

function Jaida_custom_button_template(data) {
  let msgData = { ...data.msgData };
  console.log(msgData, "isjaida_custom_button_template");
  let jaida_custom_button_template_text =
    msgData?.message?.[0]?.component?.payload?.text;
  let jaida_custom_button_template_subText =
    msgData?.message?.[0]?.component?.payload?.subText;
  let jaida_custom_button_template_List =
    msgData?.message?.[0]?.component?.payload?.buttons;

  const [formattedDate, setFormattedDate] = useState("");
  let chatWindowInstance = data.hostInstance;

  useEffect(() => {
    setFormattedDate(formatTime(msgData?.createdOn));
  }, [msgData.createdOn]);

  const getIconByTitle = (name) => {
    switch (name) {
      case "question":
        return (
          <Box>
            <QuestionMarkIcon
              sx={{
                color: "white",
                marginTop: "8px",
                fontSize: "25px",
                marginLeft: "10px",
              }}
            />
          </Box>
        );
      case "note":
        return (
          <Box
            sx={{
              color: "white",
              marginTop: "10px",
              fontSize: "25px",
              marginLeft: "15px",
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="16"
              height="16"
              viewBox="0 0 16 16"
              fill="none"
            >
              <path
                d="M14.5 10V13.5C14.5 13.7652 14.3946 14.0196 14.2071 14.2071C14.0196 14.3946 13.7652 14.5 13.5 14.5H2.5C2.23478 14.5 1.98043 14.3946 1.79289 14.2071C1.60536 14.0196 1.5 13.7652 1.5 13.5V2.5C1.5 2.23478 1.60536 1.98043 1.79289 1.79289C1.98043 1.60536 2.23478 1.5 2.5 1.5H6"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M11 1.5H14.5V5"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M14.5 1.5L8 8"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
            </svg>
          </Box>
        );
      case "headphone":
        return (
          <Box
            sx={{
              color: "white",
              marginTop: "10px",
              fontSize: "25px",
              marginLeft: "15px",
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="20"
              height="20"
              viewBox="0 0 20 20"
              fill="none"
            >
              <path
                d="M4 10.7509V6.80595C4.01172 6.0305 4.17633 5.26498 4.48441 4.55325C4.79249 3.84153 5.23797 3.19759 5.79535 2.65833C6.35272 2.11907 7.01102 1.69509 7.73253 1.41069C8.45404 1.12628 9.22458 0.987045 10 1.00095C10.7754 0.987045 11.546 1.12628 12.2675 1.41069C12.989 1.69509 13.6473 2.11907 14.2047 2.65833C14.762 3.19759 15.2075 3.84153 15.5156 4.55325C15.8237 5.26498 15.9883 6.0305 16 6.80595V10.7509"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M2.2 9H3.4C3.55913 9 3.71174 9.06585 3.82426 9.18306C3.93679 9.30027 4 9.45924 4 9.625V13.375C4 13.5408 3.93679 13.6997 3.82426 13.8169C3.71174 13.9342 3.55913 14 3.4 14H2.2C1.88174 14 1.57652 13.8683 1.35147 13.6339C1.12643 13.3995 1 13.0815 1 12.75V10.25C1 9.91848 1.12643 9.60054 1.35147 9.36612C1.57652 9.1317 1.88174 9 2.2 9V9Z"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path
                d="M17.8 14H16.6C16.4409 14 16.2883 13.9342 16.1757 13.8169C16.0632 13.6997 16 13.5408 16 13.375V9.625C16 9.45924 16.0632 9.30027 16.1757 9.18306C16.2883 9.06585 16.4409 9 16.6 9H17.8C18.1183 9 18.4235 9.1317 18.6485 9.36612C18.8736 9.60054 19 9.91848 19 10.25V12.75C19 13.0815 18.8736 13.3995 18.6485 13.6339C18.4235 13.8683 18.1183 14 17.8 14V14Z"
                stroke="white"
                strokeWidth="2"
                strokeLinecap="round"
                strokeLinejoin="round"
              />
              <path d="M12 19H16V13" stroke="white" strokeWidth="2" />
            </svg>
          </Box>
        );
      case "folder":
        return (
          <Box>
            <FolderOutlinedIcon
              sx={{
                color: "white",
                marginTop: "10px",
                fontSize: "25px",
                marginLeft: "10px",
              }}
            />
          </Box>
        );
      case "book":
        return (
          <Box>
            <AutoStoriesOutlinedIcon
              sx={{
                color: "white",
                marginTop: "10px",
                fontSize: "25px",
                marginLeft: "10px",
              }}
            />
          </Box>
        );
      default:
        return;
    }
  };

  const handleSendTitleClick = (data) => {
    // if (data.title === "Connect to AskGS Live Chat") {
    //   initiateAgentTransfer();
    //   setTimeout(() => {
    //     document.querySelector(".kore-chat-window").style.display = "none";
    //   }, 6000);
    //   // document.querySelector(".minimized").click();
    // }
    var value = data?.title.trim();
    var type = data?.type;
    if (type) {
      type = type.toLowerCase();
    }
    if (value && (type === "postback" || type === "text")) {
      chatWindowInstance.sendMessage(value);
    } else if (type === "url" || type === "web_url") {
      let a_link = data?.url;
      console.log("MSGADATA", a_link);
      if (a_link.indexOf("http:") < 0 && a_link.indexOf("https:") < 0) {
        a_link = `http:////${a_link}`;
      }
      chatWindowInstance.openExternalLink(a_link);
    }
  };

  if (
    jaida_custom_button_template_List &&
    jaida_custom_button_template_List.length > 0
  ) {
    return (
      <Box>
        {msgData.icon && (
          <div className="kore-profile-photo">
            <div
              className="user-account avtar"
              style={
                !msgData.fromAgent
                  ? { backgroundImage: `url(${msgData.icon})` }
                  : {}
              }
              title="User Avatar"
            ></div>
          </div>
        )}
        <p className="jaida_custom_button_template_text">
          {jaida_custom_button_template_text}
        </p>
        {jaida_custom_button_template_subText ? (
          <p className="jaida_custom_button_template_subText">
            {jaida_custom_button_template_subText}
          </p>
        ) : null}

        {jaida_custom_button_template_List.map((button, index) => (
          <div
            key={index}
            className="jaida_custom_button_template_card"
            onClick={() => handleSendTitleClick(button)}
          >
            <p className="jaida_custom_button_template-kore-icon" component="span">
              {getIconByTitle(button.iconType)}
            </p>
            <p className="jaida_custom_button_template_title">{button.title}</p>
            <p className="koreCustomForwordIcon">
              {" "}
              <ArrowForwardIosIcon />{" "}
            </p>
          </div>
        ))}
        {msgData.createdOn && <div className="extra-info">{formattedDate}</div>}
      </Box>
    );
  }
}

function isjaida_custom_button_template(msgData) {
  if (
    msgData.message[0].component &&
    msgData.message[0].component.payload &&
    msgData.message[0].component.payload.template_type ===
      "jaida_custom_button_template"
  ) {
    return true;
  } else {
    return false;
  }
}
export { Jaida_custom_button_template, isjaida_custom_button_template };
