import React from "react";
import ReactDOM from "react-dom/client";

class KRDomManager {
  constructor(component, renderMessage) {
    this.component = component;
    this.componentRenderMessage = renderMessage;
  }

  renderMessage(msgData) {
    if (this.componentRenderMessage(msgData)) {
      let self = this;
      let hostInstance = self.hostInstance;
      const e = React.createElement;

      const domContainer = document.createElement("li");
      if (msgData.messageId) domContainer.setAttribute("id", msgData.messageId);
      domContainer.setAttribute("data-time", msgData.createdOn);
      domContainer.setAttribute(
        "class",
        `${
          msgData.type === "bot_response" ? "fromOtherUsers" : "fromCurrentUser"
        } ${msgData.icon ? "with-icon" : ""} ${
          msgData.fromAgent ? "from-agent" : ""
        }`
      );
      if (
        [
          "Suggestion_Payload",
          "Extremely Likely",
          "Extremely Unlikely",
        ].includes(msgData?.message?.[0]?.cInfo?.body) ||
        msgData?.message?.[0]?.cInfo?.body?.subject
      ) {
        domContainer.setAttribute("class", "koreHideLiMessage");
      }
      const root = ReactDOM.createRoot(domContainer);

      root.render(
        e(this.component, {
          hostInstance: hostInstance,
          msgData: msgData,
        })
      );
      return domContainer;
    } else {
      return false;
    }
  }
}
export default KRDomManager;
