import {chatConfig } from "kore-web-sdk";

export default function formatTime(date) {
  let formattedTime = "";
  try {
    formattedTime = new Intl.DateTimeFormat("en-US", {
      hour12: true,
      hour: "2-digit",
      minute: "numeric",
    }).format(date);
    return formattedTime;
  } catch (error) {
    let currentDate = Date.now();
    formattedTime = new Intl.DateTimeFormat("en-US", {
      hour12: true,
      hour: "2-digit",
      minute: "2-digit",
    }).format(currentDate);
    return formattedTime;
  }
}


export function sendMessageToBot(text) {
  console.log("Calling Send msg", text)
  var messageToBot = {};
    messageToBot["clientMessageId"] = new Date().getTime();
    messageToBot["message"] ={
          "body": text
    }
    messageToBot["botInfo"] = {
      "chatBot": chatConfig.botOptions.botInfo.name,
      "taskBotId": chatConfig.botOptions.botInfo._id
    }
    messageToBot["resourceid"] = "/bot.message";
    return messageToBot;
}

