import { chatWindow, chatConfig } from "kore-web-sdk";
import SDKReactComponentManager from "./KRDomManager";
import {
  MessageTemplate,
  isMessageTemplate,
} from "./Templates/MessageTemplate/MessageTemplate";
import { createRoot } from "react-dom/client";
import ExitTemplate from "./Templates/Chat Utility Templates/ExitTemplate";
import ChatControls from "./Templates/Chat Utility Templates/ChatControls";
import {
  AskGsTemplate,
  isAskGsTemplate,
} from "./Templates/Custom AskGS/customAskGSTemplate";
import {
  IrisButtonTemplate,
  isIrisTemplate,
} from "./Templates/Custom IRIS Template/customIRISTemplate";
import {
  ChipButton,
  isChipButtonTemplate,
} from "./Templates/CustomChip Template/customChipTemplate";
import {
  LangTemplate,
  isLangTemplate,
} from "./Templates/CustomLang Template/customLangTemplate";
import {
  CustomFeedbackTemplate,
  isFeedbackTemplate,
} from "./Templates/CustomFeedBackTemplate/CustomFeedBackTemplate";
import { sendMessageToBot } from "./utilities";
import {
  CustomSuggestionBox,
  isSuggestionBox,
} from "./Templates/SuggitionBox Template/suggitionBox";
import ChatInputField from "./Templates/Chat Utility Templates/ChatInputField";
import { Jaida_custom_button_template, isjaida_custom_button_template } from "./Templates/jaida_custom_button_template/jaida_custom_button_template";

export const initiateKoreChat = (
  selectedLanguage,
  sessionDetails,
  fetchHistory,
  forceStopHistory
) => {
  return new Promise((resolve, reject) => {
    chatConfig.loadHistory = fetchHistory;
    chatConfig.isSendButton = false;
    chatConfig.multiPageApp.enable = true;
    var botOptions = chatConfig.botOptions;
    var jquerySelector = chatWindow.prototype.$;
    var initiateStatus = 0;
    botOptions.JWTUrl = "https://demo.kore.net/users/sts";

    botOptions.botInfo = {
      name: "JAIDA",
      _id: "st-d8461592-cfbf-54e1-9409-c4cbab4a7e1a",
      customData: {
        lang: selectedLanguage,
        userSessionDetails: sessionDetails,
      },
    };
    botOptions.clientId = "cs-d14b7b0c-4547-500d-9b2b-849a452ddd8e";
    botOptions.clientSecret = "cYC7B013/5UwjevawuOVYKXxJpjOzxKy8rY+PJuP1Xs=";

    var chatWindowInstance = new chatWindow(chatConfig);

    chatWindowInstance.on("viewInit", (chatElement) => {
      const chatContainer =
        jquerySelector(".kore-chat-window").find(".kore-chat-body");

      //Adding chat exit functionality
      const createExitChatContainer = jquerySelector("<div>", {
        class: "koreHideExitChatContainer",
      });
      if (chatContainer) {
        chatContainer.prepend(createExitChatContainer);
      }

      const getKoreExitContainer = document.querySelector(
        ".koreHideExitChatContainer"
      );
      if (getKoreExitContainer) {
        const root = createRoot(getKoreExitContainer);
        root.render(<ExitTemplate chatWindowInstance={chatWindowInstance} />);
      }

      //Adding custom chat buttons
      const createCustomButtonsContainer = jquerySelector("<div>", {
        class: "koreCustomButtonsContainer",
      });
      const buttonContainer =
        jquerySelector(".kore-chat-window").find(".chat-box-controls");
      if (buttonContainer) {
        buttonContainer.prepend(createCustomButtonsContainer);
      }

      const getCustomButtonsContainer = document.querySelector(
        ".koreCustomButtonsContainer"
      );
      if (getCustomButtonsContainer) {
        const root = createRoot(getCustomButtonsContainer);
        root.render(<ChatControls chatWindowInstance={chatWindowInstance} />);
      }

      const chatInputBoxContainer = document.querySelector(".footerContainer");
      if (chatInputBoxContainer) {
        const root = createRoot(chatInputBoxContainer);
        root.render(<ChatInputField chatWindowInstance={chatWindowInstance} />);
      }

      //Add text to bot header title
      var botTitle = jquerySelector(".header-title");
      if (botTitle.length) {
        botTitle.removeAttr("id");
        botTitle.removeAttr("class");
        botTitle.attr("id", "headerTitle");
        // Check if the region is EMEA  NA, ASPAC
        let region = sessionDetails?.region.split(",");
        if (
          region.includes("NA") ||
          region.includes("EMEA") ||
          region.includes("ASPAC")
        ) {
          botTitle.attr("title", "JAIDA - Welcome EMEA");
          botTitle.html(
            "<strong>JAIDA</strong> <br/> <span>With Legal Answers powered by Lexi</span>"
          );
        } else {
          botTitle.attr("title", "JAIDA");
          botTitle.html(
            "<strong>JAIDA</strong> <br/> <span>J&J AI Digital Assistant</span>"
          );
        }
      }

      // Inside Header changes
      const chatHeaderEl = chatElement.chatEle.find(".kore-chat-header");
      let shadowContainer = document.createElement("div");
      shadowContainer.className = "Kore_header_Shadow";
      chatHeaderEl.append(shadowContainer);

      let BlockBtn = chatElement.chatEle.find(".customBranding-theme");
      BlockBtn.css({
        display: "block",
      });

      //Adding backgroung on exit chat container
      const shadowDialogContainer = document.createElement("div");
      shadowDialogContainer.className = "shadow-dialog-container";
      const completeChatContainer = document.querySelector(".kore-chat-window");
      completeChatContainer.append(shadowDialogContainer);
      shadowDialogContainer.style.display = "none";

      //ADDING REMESH WRITEN CODE
      chatWindow.prototype.openExternalLink = function (link_url) {
        if (link_url?.indexOf("intent_") > -1) {
          let intent = link_url.split("intent_")[1];
          intent = intent.replace("_", " ");
          let messageToBot = sendMessageToBot(intent);
          chatWindowInstance.bot.sendMessage(messageToBot, function (err) {
            if (err) console.log("*********", err);
          });
        } else {
          var aTag = document.createElement("a");
          aTag.href = link_url;
          aTag.target = "_blank";
          aTag.rel = "noopener noreferrer";
          console.log(aTag);
          aTag.click();
        }
      };
    });

    chatWindowInstance.on("beforeRenderMessage", (chatElement) => {
      const suggitionChipsContainer = document.querySelector(
        ".koreChip_chipButtonContainer"
      );

      if (suggitionChipsContainer) {
        suggitionChipsContainer.style.display = "none";
      }
    });

    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(MessageTemplate, isMessageTemplate)
    );
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(AskGsTemplate, isAskGsTemplate)
    );
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(IrisButtonTemplate, isIrisTemplate)
    );
   
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(ChipButton, isChipButtonTemplate)
    );
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(LangTemplate, isLangTemplate)
    );
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(CustomFeedbackTemplate, isFeedbackTemplate)
    );  
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager(CustomSuggestionBox, isSuggestionBox)
    );  
    
    chatWindowInstance.templateManager.installTemplate(
      new SDKReactComponentManager( Jaida_custom_button_template, isjaida_custom_button_template)
    );

    

    //Handling user id
    if (chatWindowInstance.config.multiPageApp.enable) {
      let _userIdentityStore = localStorage.getItem("kr-cw-uid");
      if (_userIdentityStore) {
        botOptions.userIdentity = _userIdentityStore;
        localStorage.removeItem("kr-cw-uid");
      } else {
        botOptions.userIdentity = sessionDetails.email;
        localStorage.removeItem("kr-cw-uid");
        localStorage.removeItem("kr-cw-state");
      }
    } else {
      botOptions.userIdentity = sessionDetails.email;
      localStorage.setItem("kr-sw-uid", botOptions.userIdentity);
    }

    //Handling history reload
    if (forceStopHistory && window.customChatInstance) {
      window.customChatInstance.config.botOptions.loadHistory = false;
      window.customChatInstance.config.botOptions.botInfo.taskBotId =
        "st-d8461592-cfbf-54e1-9409-c4cbab4a7e1a";
      window.customChatInstance.config.botOptions.botInfo.chatBot = "JAIDA";
      chatWindowInstance = window.customChatInstance;
      document.querySelector(".minimized").click();
    } else if (forceStopHistory && !window.customChatInstance) {
      document.querySelector(".minimized").click();
    } else {
      window.customChatInstance = chatWindowInstance;
      chatWindowInstance.show(chatConfig);
      initiateStatus = 200;
    }
    resolve(initiateStatus);
  });
};
