import React, { useState } from "react";
import ThumbUpOffAltIcon from "@mui/icons-material/ThumbUpOffAlt";
import ThumbDownOffAltIcon from "@mui/icons-material/ThumbDownOffAlt";
import "./CustomFeedbackTemplate.css";

function CustomFeedbackTemplate(data) {
  const [clickedIcon, setClickedIcon] = useState("All");

  let msgData = { ...data?.msgData };

  let chatWindowInstance = data.hostInstance;

  const handleThumbClick = (type) => {
    if (type !== clickedIcon) {
      chatWindowInstance.sendMessage(
        type === "yes" ? "Extremely Likely" : "Extremely Unlikely"
      );
      setClickedIcon(type);
    }
  };

  if (msgData.fromHistory) {
    return null;
  } else {
    return (
      <>
        <div
          className={`kore-feedback-template-container ${
            clickedIcon ? "clicked" : ""
          }`}
        >
          <div className="kore-feedback-text">Did I answer your question?</div>
          {clickedIcon === "All" && (
            <>
              <div
                className="kore-feedback-icon thumb-up"
                onClick={() => handleThumbClick("yes")}
              >
                <ThumbUpOffAltIcon sx={{ color: "gray" }} />
              </div>
              <div
                className="kore-feedback-icon thumb-down"
                onClick={() => handleThumbClick("no")}
              >
                <ThumbDownOffAltIcon sx={{ color: "gray" }} />
              </div>
            </>
          )}
          {clickedIcon === "yes" && (
            <div
              className={`kore-feedback-icon ${
                clickedIcon === "yes" ? "yesClicked" : ""
              }`}
              onClick={() => handleThumbClick("yes")}
            >
              <ThumbUpOffAltIcon sx={{ color: "gray" }} />
            </div>
          )}
          {clickedIcon === "no" && (
            <div
              className={`kore-feedback-icon ${
                clickedIcon === "no" ? "noClicked" : ""
              }`}
              onClick={() => handleThumbClick("no")}
            >
              <ThumbDownOffAltIcon sx={{ color: "gray" }} />
            </div>
          )}
        </div>
      </>
    );
  }
}

function isFeedbackTemplate(msgData) {
  if (
    msgData.message[0].component &&
    msgData.message[0].component.payload &&
    msgData.message[0].component.payload.template_type === "feedbackTemplate"
  ) {
    return true;
  } else {
    return false;
  }
}

export { CustomFeedbackTemplate, isFeedbackTemplate };
